<template>
  <div class="page">
    <div class="page-header">
      <HeaderTwo :titleVal="title"></HeaderTwo>
    </div>
    <div class="page-content">
        <van-swipe :autoplay="3000">
          <van-swipe-item v-for="(image, index) in images" :key="index">
            <img v-lazy="image" @click="swipeImgPreview"/>
          </van-swipe-item>
        </van-swipe>
        <div>
          <!--  -->
          <div class="detail">
            <div class="title" name="title">LANCOME</div>
            <div class="price" name="price">3500.00</div>
          </div>
          <div style='margin-top:10px;'>
            <van-field
              v-model="specifValue"
              name="address"
              label=""
              icon="arrow"
              placeholder=""
              @click="onSpecif"
              :border="false"
              :readonly="true"
              title-width="40px"
            />
            <van-popup
              v-model="show"
              position="bottom"
              bind:click-overlay="onClose"
            >
              <div class='specif-count'>
                <div class='specif-header'>
                  <div class='specif-img'>
                    <img class='img' src='../assets/shop1.png'/>
                  </div>
                  <div class='specif-detail'>
                    <div class='specif-content'></div>
                    <div class='specif-price'>3500.00</div>
                  </div>
                  <div class='specif-close-icon'>
                    <van-icon name="close" size="20px" @click='closeSpecif'/>
                  </div>
                </div>
                <div class='specif-cation'>
                  <div class='search-history-title'></div>
                  <div class='search-cell'>
                    <div class='search-option'></div>
                    <div class='search-option'></div>
                    <div class='search-option'></div>
                    <div class='search-option'></div>
                    <div class='search-option'></div>
                  </div>
                </div>
                <div style='margin-bottom:44px;'>
                  <van-cell title="" size="large">
                    <van-stepper name="count" v-model="specifCount" bind:change="onCount"/>
                  </van-cell>
                </div>
                <van-goods-action>
                  <van-goods-action-big-btn text=""/>
                  <van-goods-action-big-btn primary text=""/>
                </van-goods-action>  
              </div>
            </van-popup>
            <!--  -->
            <div class="separate"></div>
            <van-cell title="(0)" size="large" :border="false"></van-cell>
            <div class="text-remark">~</div>
            <div class="separate"></div>
          </div>
          <!--  -->
          <div style='margin-top:10px;margin-bottom:50px;'>
            <van-cell title="" size="large"></van-cell>
            <div v-for="(item, index) in detailImg" :key="index">
              <img class="image_detail" :src="item.img"/>
            </div>
          </div>
        </div>
        <!--  -->
        <van-goods-action>
          <van-goods-action-mini-btn icon="star-o" text=""/>
          <van-goods-action-mini-btn icon="cart-o" text=""/>
          <van-goods-action-big-btn text=""/>
          <van-goods-action-big-btn primary text=""/>
        </van-goods-action>
    </div>
  </div>
</template>

<script>
import { ImagePreview } from 'vant';
import HeaderTwo from '@/components/HeaderTwo';
export default {
  name: 'GoodsDetail',
  components:{
    HeaderTwo
  },
  data () {
    return {
      title:"",   
      isLike: true,
      images: [
        require('../assets/s-banner1.jpg'),
        require('../assets/s-banner2.jpg')
      ],
      // 
      detailImg: [
        {img:require('../assets/sp-detail1.png')},
        {img:require('../assets/sp-detail2.png')}
      ],
      show: false,
      specifValue: 1,
      specifCount: 1
    }
  },
  created () {
    this.$toast("id:"+this.$route.query.goodsId); 
  },
  mounted () {
    
  },
  methods:{
    //
    onSpecif: function () {  
      this.show= true
    },
    closeSpecif: function () {
      this.show= false
    },
    //
    onClose: function () {
      this.show= false
    },
    //
    addLike() {
      this.isLike= !this.data.isLike
    },
    //
    toCar() {
      
    },
    //
    immeBuy() {
      
    },
    //
    swipeImgPreview: function () {
      ImagePreview(this.images);
    }
  }
  
}
</script>
<style scoped>
.van-swipe-item img{
  float: left;
  width: 100%;
}
.detail {
  display: flex;
  flex-direction: column;
  margin-bottom: 0px;
}
.title {
  background: #ffffff;
  color: black;
  font-size: 16px;
  padding: 10px 15px;
  text-align: justify;
}
.price {
  background: #ffffff;
  color: #3982f6;
  font-size: 16px;
  padding: 10px 15px;
}
.line_flag {
  width: 40px;
  height: 1px;
  display: inline-block;
  margin: 10px auto;
  background-color: gainsboro;
  text-align: center;
}
.line {
  width: 100%;
  height: 1px;
  display: inline-block;
  margin: 10px 0px;
  background-color: gainsboro;
  text-align: center;
}
.detail-nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  float: left;
  background-color: #fff;
  position: fixed;
  bottom: 0;
  right: 0;
  z-index: 1;
  width: 100%;
  height: 50px;
}
.button-green {
  background-color: #4caf50; /* Green */
}
.button-red {
  background-color: #f44336; /*  */
}
.image_detail {
  width: 100%;
}
button {
  color: white;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 15px;
  border-radius: 0px;
  width: 50%;
  height: 100%;
  line-height: 50px;
}
.detail-nav image {
  width: 35px;
  height: 25px;
  margin: 10px 20px;
}
.line_nav {
  width: 2.5px;
  height: 100%;
  background-color: gainsboro;
}
/*  */
.temp {
  height: 50px;
}
.text-remark {
  background: #ffffff;
  color: #aaaaaa;
  display: block;
  font-size: 12px;
  padding: 10px 15px;
}

.specif-count{
  float: left;
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  padding: 10px 0px;
  box-sizing: border-box;
}
.specif-count .specif-header{
  padding: 10px 15px;
  box-sizing: border-box;
}
.specif-count .specif-img{
  float: left;
  width: 35%;
  height: 120px;
  margin-right: 0.2rem;
}
.specif-count .specif-img .img{
  width: 100%;
  height: 120px;
}
.specif-count .specif-detail{
  float: left;
  width: 55%;
}
.specif-count .specif-close-icon{
  float: left;
  width: 5%;
  margin-top: -15px;
}
.specif-count .specif-price{
  color: #3982f6;
  font-size:14px;
}
.specif-count .specif-content{
  font-size: 18px;
}

.specif-count .specif-cation{
  float: left;
  width: 100%;
}
.search-history-title{
  float: left;
  width: 100%;
  font-size: 16px;
  padding: 10px 15px;
  box-sizing: border-box;
}
.search-cell{
  padding: 10px 0px;
}
.search-cell .search-option{
  float: left;
  margin: 5px 15px;
  text-align: center;
  padding: 5px 15px;
  font-size: 14px;
  background: #f2f2f2;
  color: #666666;
  border-radius: 25px;
}

.van-button--danger{
  background-color: #3982f6 !important;
  border:2rpx solid #3982f6 !important;
}
.van-info{
  background-color: #3982f6 !important;
} 
button{
  border: 0;
}
</style>



