<template>
  <div class="page">
    <div class="page-header">
      <HeaderTwo :titleVal="title"></HeaderTwo>
    </div>
    <div class="page-content">
        <form @submit.prevent="formSubmitAddress">
          <div>
            <van-cell-group>
              <van-field
                v-model="formText.username"
                name="username"
                clearable
                icon="arrow"
                placeholder=""
                bind:click-icon="onClickIcon"
              />
              <van-field
                v-model="formText.phone"
                name="phone"
                clearable
                icon="arrow"
                placeholder=""
                bind:click-icon="onClickIcon"
              />
              <van-field
                v-model="formText.address"
                name="address"
                icon="arrow"
                placeholder=""
                @click.native="onAddress"
                disabled
              />
              <van-popup
                v-model="showSet"
                position="bottom"
                bind:click-overlay="onClose"
              >
                <van-area :area-list="areaList" @cancel="closeArea" @confirm="okArea"/>
              </van-popup>
              <van-field
                v-model="formText.city"
                name="city"
                clearable
                icon="arrow"
                placeholder=""
              />             
              <van-switch-cell
                v-model="formText.checked"
                name="switch"
                title=""
              />
            </van-cell-group>   
          </div>
          <div>
            <button class='address-add-btn'  form-type="submit"></button>
          </div>
        </form>
    </div>
  </div>
</template>

<script>
import HeaderTwo from '@/components/HeaderTwo';
import areaVal from '@/utils/areaList'
export default {
  name: 'AddressAdd',
  components:{
    HeaderTwo
  },
  data () {
    return {
      title:"",
      formText:{
        username:"",
        phone:"",
        address:"",
        city:"",
        checked:false
      },
      showSet: false,
      areaList: areaVal.areaList,
    }
  },
  created () {

  },
  mounted () {

  },
  methods:{
      
      //
      onAddress: function () {
        this.showSet= true 
      },
      //
      onClose: function () {
        this.showSet= false 
      },
      //
      closeArea: function () {
        this.showSet= false 
      },
      //
      okArea: function (value) {
        const addressVal = value;
        console.log(addressVal);
        const provinceVal=addressVal[0].name+",";
        const cityVal = addressVal[1].name + ",";
        const countyVal = addressVal[2].name;
        
        this.formText.address= provinceVal + cityVal + countyVal,
        this.showSet= false 
      },
      //
      formSubmitAddress: function() {
          console.log(this.formText);
      }
  }
  
}
</script>
<style scoped>
.address-add-btn{
  position: fixed;
  bottom: 0;
  z-index: 1;
  width: 100%;
  height: 44px;
  line-height:44px; 
  background:linear-gradient(63deg,rgba(50,125,218,1),rgba(50,66,218,1));
  color: #ffffff;
  font-size: 16px;
  text-align: center;
  border: 0;
  border-radius: 0;
}
</style>



